<template>
    <div class="row"  >
        
        <div class="col-lg-12">
            <form class="card" @submit.prevent="submit()">
                
                <div class="card-body" >
                    <div class="row">
                        <div class="col-md-12">
                            <h3>Entrada de item para lavagem <span v-if="form.id">: {{form.id}}</span> </h3>
                        </div>
                        <div class="col-md-12">
                            <div class="row">
                                <div class="col-sm-7" >
                                    <container-componente :validator="$v.form.parceiro_id" label="Cliente">
                                        <vue-select style="height: calc(1.5em + 0.94rem - 6px)"  placeholder="Comece a digitar para buscar" :disabled="editando"
                                            :options="parceiros" label="apelido" @input="selecionouCliente() "
                                            @blur="$v.form.parceiro_id.$touch" @search="buscarParceirosDebounce"
                                            v-model="form.parceiro"
                                        >
                                            <template slot="option" slot-scope="option">
                                                {{ option.apelido }} / {{ option.razao_social}}
                                            </template>
                                            <div slot="no-options">Nenhum item encontrado!</div>
                                            <div class="spinner" v-show="buscandoParceiros">Buscando...</div>
                                        </vue-select>
                                         <a v-if="!editando" style="float:right" @click="novoParceiro()" class="" data-toggle="modal" href="#modal-adicionar-cliente">
                                            <i class="bx bxs-plus-square font-size-16 align-middle mr-2"></i> Novo Cliente
                                        </a>
                                    </container-componente>
                                        
                                </div>
                                <div class="col-sm-5">
                                    <label class="control-label">Dados do Cliente</label>
                                    <div style="margin-bottom: 20px">
                                        <div v-if="form.parceiro && form.parceiro.razao_social"> <strong>Nome: </strong> {{form.parceiro ? form.parceiro.razao_social : ''}} </div>
                                        <div v-if="form.parceiro && form.parceiro.fone"> <strong>Telefone: </strong> {{form.parceiro ? form.parceiro.fone : ''}} </div>
                                        <div v-if="form.parceiro && form.parceiro.email"> <strong>Email: </strong> {{form.parceiro ? form.parceiro.email : ''}}</div>
                                        <div v-if="form.parceiro"> <strong>Saldo <span v-if="saldo_cliente <0"> Negativo</span> <span v-else> Disponível</span>: </strong> 
                                             {{saldo_cliente | currency($store.state.moeda)}}
                                        </div>                                        
                                    </div>
                                </div>

                                <div class="col-sm-7 mt-20" >
                                    <container-componente :validator="$v.form.descricao" label="Descrição">
                                      <textarea class="form-control" rows="2" placeholder="Descreva de forma geral os itens de entrada" 
                                        v-model="form.descricao" @blur="$v.form.descricao.$touch()"
                                      ></textarea>
                                    </container-componente>
                                </div>

                                <div class="col-sm-5 mt-20" >
                                    <container-componente :validator="$v.form.data_entrada" label="Data de Entrada">
                                      <date-picker v-model="form.data_entrada" :clearable="false" :format="'DD/MM/YYYY'" :disabledDate="desabilitaDataFutura"></date-picker>
                                    </container-componente>
                                </div>

                            </div>
 
                            <div class="row" v-show="form.parceiro">
                                <div class="col-sm-12">
                                    <hr>
                                </div>
                                <div class="col-sm-12">
                                    <h3>Itens</h3>
                                </div>
                                <div class="col-sm-12" >

                                    <div class="table-responsive">
                                        <table class="table table-centered table-nowrap mb-0">
                                            <thead>
                                                <tr>
                                                    <th scope="col">Item</th>
                                                    <th scope="col" style="min-width:275px">Serviço</th>
                                                    <th scope="col">Descrição</th>
                                                    <th scope="col">Valor</th>
                                                    <th scope="col">Qtde</th>
                                                    <th scope="col">Subtotal</th>
                                                    <th>#</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(servico, index) in form.servicos" :key="index">
                                                    <td >
                                                        <span style="width:100%"># {{(index + 1)}}</span>
                                                        <span>&nbsp;</span>
                                                    </td>
                                                    
                                                    <td>
                                                        <container-componente :validator="$v.form.servicos.$each[index].servico_item_id"  >
                                                            <select v-model="servico.servico_item" class="form-control" @change="selecionouServico(servico)">
                                                                <option value="" disabled >Selecione uma opção</option>
                                                                <option v-for="s in servicos_item" :key="s.id"  :value="s">{{s.nome}}</option>
                                                                
                                                            </select>
                                                        </container-componente>
                                                        <span>&nbsp;</span>
                                                    </td>
                                                    <td>
                                                        <input-componente
                                                            v-model="servico.descricao"
                                                            :validator="$v.form.servicos.$each[index].descricao"
                                                        />
                                                        <span>&nbsp;</span>
                                                    </td>
                                                    <td>
                                                        <container-componente :validator="$v.form.servicos.$each[index].valor_unitario" >
                                                            <money :disabled="(!servico.servico_item)"
                                                                class="form-control"
                                                                v-model.lazy="servico.valor_unitario"
                                                                @input="servico.valor = servico.valor_unitario * servico.qtde"
                                                                v-bind="preco"
                                                            ></money>
                                                            <span v-if="servico.servico_item" style="cursor:pointer" v-popover="{ name: 'info-precos'+index }">
                                                                <span style="color: red" v-if="servico.valor_unitario < servico.servico_item.valor_unitario && (servico.servico_item.valor_unitario_cliente <= 0 || servico.valor_unitario < servico.servico_item.valor_unitario_cliente )">
                                                                    <span class="fas fa-exclamation-triangle"></span> Valor Baixo
                                                                </span>                                                                
                                                                <span v-else >
                                                                    <span class="fas fas fa-money-bill"></span> Preço
                                                                </span>
                                                            
                                                                <popover :name="'info-precos'+index" style="left: none; top:none; text-align: center">
                                                                    <p>Tabela: {{servico.servico_item.valor_unitario | currency($store.state.moeda)}}</p>
                                                                    <p>Cliente: <span v-if="servico.servico_item.valor_unitario_cliente > 0">{{servico.servico_item.valor_unitario_cliente | currency($store.state.moeda)}}</span><span v-else> - </span></p>
                                                                   
                                                                </popover>
                                                            </span>    
                                                        </container-componente>
                                                        <span v-if="!servico.servico_item">&nbsp;</span>
                                                    </td>
                                                    <td>
                                                        <container-componente :validator="$v.form.servicos.$each[index].qtde" >
                                                            <money :disabled="(!servico.servico_item)"
                                                                class="form-control"
                                                                v-model.lazy="servico.qtde"
                                                                @input="servico.valor = servico.valor_unitario * servico.qtde"
                                                                v-bind="qtde(servico.servico_item ? servico.servico_item.medida.decimais : 0,servico.servico_item ? servico.servico_item.medida.sigla : 'Un' )"
                                                            ></money>
                                                        </container-componente>
                                                        <span>&nbsp;</span>
                                                    </td>
                                                    <td>
                                                        <container-componente :validator="$v.form.servicos.$each[index].valor" >
                                                            <money :disabled="true"
                                                                class="form-control"
                                                                v-model.lazy="servico.valor"
                                                                v-bind="preco"
                                                            ></money>
                                                        </container-componente>
                                                        <span>&nbsp;</span>
                                                    </td>
                                                    <td>
                                                        <a v-if="form.servicos.length > 1" href="#" @click.prevent="removerServico(index)"><i class="mdi mdi-trash-can danger"></i></a>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td colspan="7" class="text-left">
                                                        <button type="button" class="btn btn-sm btn-primary" v-if="!editando" @click.prevent="adicionarServico()"><i class="mdi mdi-clipboard-plus font-size-18"></i> Adicionar Serviço</button>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                        
                                    </div>

                                </div>
                            </div>

                            <div class="row">
                                <div class="col-sm-12">
                                    <hr>
                                </div>
                                <div class="col-sm-12">
                                    <h3>Total</h3>
                                </div>
                                <div class="col-sm-6">
                                </div>
                                <div class="col-sm-6">
                                   <div class="table-responsive">
                                        <table class="table table-centered table-nowrap mb-0">
                                            <thead>
                                                <tr>
                                                    <th>Item</th>
                                                    <th>Quantidade</th>
                                                    <th>Valor Total</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in itens_por_tipo" :key="index" >
                                                    <th>{{item.servico_item.nome}}</th>
                                                    <td>{{item.qtde_total | qtde(item.servico_item.medida.decimais)}} {{item.servico_item.medida.sigla}}</td>
                                                    <td>{{item.valor_total | currency($store.state.moeda)}}</td>
                                                </tr>
                                                <tr>
                                                    <th>Total da Os</th>
                                                    <th>{{form.servicos.length}}</th>
                                                    <td>{{valor_total | currency($store.state.moeda)}}</td>
                                                </tr>                                                
                                            </tbody>
                                        </table>
                                        
                                    </div>
                                </div>
                                <div class="col-md-12 button-items text-center" v-if="precisa_confirmar_valor_zerado">
                                    <h4 style="color: red">Existem itens de serviços com valores zerados!</h4>
                                    <div class="custom-control custom-checkbox">
                                        <input type="checkbox" class="custom-control-input" id="check_confirma_zerado" v-model="confirmar_valor_zerado" >
                                        <label class="custom-control-label" for="check_confirma_zerado">Confirmo que Existem Serviços Aprovados com Valor Zerado</label>
                                    </div>
                                </div>
                            </div>

                        </div>
                            
                    </div>
                </div>
                <hr>
                
                <div class="row">
                    <div class="col-md-12 text-center" v-if="erros || sucesso">
                        <mensagem-componente :titulo="errosTitulo" v-if="erros" :erros="erros" @fechar="erros = null"></mensagem-componente>
                        <mensagem-componente v-if="sucesso" :sucesso="sucesso" @fechar="sucesso = null"></mensagem-componente>
                    </div>
                    <div class="col-md-12 text-center">
                        <router-link class="btn btn-warning waves-effect waves-light w-sm" title="Voltar" :to="{name: 'ordemServicoLavanderia'}">
                            <i class="bx bx-arrow-back d-block font-size-16"></i> Voltar
                        </router-link>
                        <button type="submit"  class="btn btn-primary waves-effect waves-light w-sm" title="salvar">
                            <i class="bx bxs-save d-block font-size-16"></i> Salvar
                        </button>
                    </div>
                    
                </div>
                     
            </form>
        </div>
      
        <div class="modal fade" id="modal-adicionar-cliente" tabindex="-1" aria-labelledby="modal-adicionar-cliente-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-lg modal-dialog-scrollable" style="min-height:450px">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-adicionar-clienteTitle"> Adicionar Novo Cliente </h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    <form class="modal-body" v-if="parceiroModal">
                        <div class="row">

                            <label class="col-sm-3 col-form-label" >Tipo de Pessoa</label>
                            <div class="col-sm-9">
                                <container-componente :validator="parceiroModal.tipo_pessoa">
                                    <div class="form-check mb-3">
                                        <input class="form-check-input" type="radio" name="tipo_pessoa" id="pessoa_fisica" :value="'FISICA'" v-model="parceiroModal.tipo_pessoa">
                                        <label class="form-check-label" for="pessoa_fisica">
                                            Pessoa Física
                                        </label>
                                    </div>
                                    <div class="form-check mb-3">
                                        <input class="form-check-input" type="radio" name="tipo_pessoa" id="pessoa_juridica" :value="'JURIDICA'" v-model="parceiroModal.tipo_pessoa">
                                        <label class="form-check-label" for="pessoa_juridica">
                                            Pessoa Jurídica
                                        </label>
                                    </div>
                                </container-componente>
                            </div>

                            <label class="col-sm-3 col-form-label">Apelido</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="parceiroModal.apelido"
                                    :validator="$v.parceiroModal.apelido"
                                    @blur="$v.parceiroModal.apelido.$touch"
                                    />
                            </div>
                            
                            <label class="col-sm-3 col-form-label">{{parceiroModal.tipo_pessoa == 'JURIDICA' ? 'Razão Social' : 'Nome Completo'}}</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="parceiroModal.razao_social"
                                    :validator="$v.parceiroModal.razao_social"
                                    @blur="$v.parceiroModal.razao_social.$touch"
                                    />
                            </div>

                            <label v-show="parceiroModal.tipo_pessoa == 'JURIDICA'" class="col-sm-3 col-form-label">Nome Fantasia</label>
                            <div v-show="parceiroModal.tipo_pessoa == 'JURIDICA'" class="col-sm-9">
                                <input-componente
                                    v-model="parceiroModal.nome_fantasia"
                                    :validator="$v.parceiroModal.nome_fantasia"
                                    @blur="$v.parceiroModal.nome_fantasia.$touch"
                                    />
                            </div>

                            <label class="col-sm-3 col-form-label">Telefone</label>
                            <div class="col-sm-9">
                                <input-componente
                                :mask="['(##) ####-####', '(##) #####-####']"
                                    v-model="parceiroModal.fone"
                                    :validator="$v.parceiroModal.fone"
                                    @blur="$v.parceiroModal.fone.$touch"
                                    />
                            </div> 

                            <label class="col-sm-3 col-form-label">Email</label>
                            <div class="col-sm-9">
                                <input-componente
                                    autofocus="true"
                                    v-model="parceiroModal.email"
                                    :validator="$v.parceiroModal.email"
                                    @blur="$v.parceiroModal.email.$touch"
                                    />
                            </div>
                        </div>
                        
                    </form>
                    <div v-else class="modal-body"></div>
                    <div class="modal-footer">
                        <div class="row">
                            <div class="col-md-12" v-if="erroItemModal">
                                <mensagem-componente titulo="Erro!"  :erros="erroItemModal" @fechar="erroItemModal = null"></mensagem-componente>
                            </div>
                            <div class="col-md-12" v-if="sucessoItemModal">                                
                                <mensagem-componente  :sucesso="sucessoItemModal" @fechar="sucessoItemModal = null"></mensagem-componente>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12">
                                <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                                <button type="button" class="btn btn-primary" v-if="!sucessoItemModal" :disabled="!parceiroModal || $v.parceiroModal.invalid" @click.prevent="salvarParceiro()"><i class="mdi mdi-content-save font-size-18"></i> Salvar </button>
                            </div>

                        </div>
                        
                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>
    </div>
</template>

<script>

import { required, requiredIf, minValue, maxLength, email } from "vuelidate/lib/validators";
import { CaixaService, OrdemServicoService, ParceiroService, ServicoItemService } from '../../services'

import moment from 'moment';

const parceiroService = new ParceiroService();
const servicoItemService = new ServicoItemService();
const ordemServicoService = new OrdemServicoService();
const caixaService = new CaixaService()

export default {
    data() {
        return {
            form: {
                id: null,
                tipo: 'LAVANDERIA',
                parceiro_id: '',
                servicos: [],
                status: 'ANDAMENTO',
                data_entrada: new Date()
            },
            parceiros: [],
            buscandoParceiros: false,
            parceiroModal: null,
            saldo_cliente: null,
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
            timer: null,

            erroItemModal: null,
            sucessoItemModal:null,            
            confirmar_valor_zerado: false,
            preco:{
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            },

            servicos_item: []
        }
    
    },
    validations() {
        return {

            parceiroModal: {
                apelido: { required, maxLength:maxLength(50)},
                razao_social: { required, maxLength:maxLength(200)},
                nome_fantasia: { required: requiredIf(function(){ return this.form.tipo_pessoa == 'JURIDICA' && !this.form.estrangeiro }), maxLength:maxLength(200)},
                tipo_pessoa: { required, maxLength:maxLength(20)},
                tipo_documento: {maxLength:maxLength(20)}, //{ required: requiredIf(function(){ return this.form.estrangeiro }), maxLength:maxLength(20)},
                documento: {maxLength:maxLength(20)}, //{ required, maxLength:maxLength(20), validarCpfCnpj: validarCpfCnpj(this.form.tipo_pessoa, this.form.estrangeiro)},
                rg_ie: { maxLength:maxLength(20)},
                rg_ie_estado: { maxLength:maxLength(2)},  //required: requiredIf(function(){ return this.form.tipo_pessoa == 'FISICA' && this.form.rg_ie.length > 0 && !this.form.estrangeiro }), maxLength:maxLength(2)},
                im: { maxLength:maxLength(20)},
                
                ativo: { required },
                cliente: { required },
                fornecedor: { required },
                cidade_id: { },
                endereco_tipo: { maxLength:maxLength(20)},
                logradouro: { maxLength:maxLength(100)},
                numero: { maxLength:maxLength(10)},
                complemento: { maxLength:maxLength(100)},
                bairro: { maxLength:maxLength(100)},
                cep: {  maxLength:maxLength(10)}, //required: requiredIf(function(){ return !this.form.estrangeiro }),
                fone: { required, maxLength:maxLength(20)},
                email: { email, maxLength:maxLength(150)},
            },

            form: {
                parceiro_id: {required},
                descricao: {maxLength: maxLength(200)},
                observacoes: {},
                status: {},

                aprovacao_data: {},
                aprovacao_observacoes: {},
                    
                valor: {},

                servicos: {
                    required,
                    $each: {
                        servico_item_id: {required},
                        descricao: {maxLength: maxLength(200)},
                        valor: {required, minValue: minValue(0)},
                        valor_unitario: {required, minValue: minValue(0)},
                        qtde: {required, minValue: minValue(0.001)},
                        aprovado: {},
                    }
                }                
                
            },
        }
        

    },
    
    methods: {

        submit: function(){
            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }

            if(this.precisa_confirmar_valor_zerado && !this.confirmar_valor_zerado){
                this.erros = "Existem serviços aprovados com valores ZERADOS, por favor confirme esta ação!"
                return false;
            }

            this.form.valor_subtotal = this.valor_total;
            this.form.valor_total = this.valor_total;
            this.form.valor = this.valor_total;
            let obj = {...this.form};
            obj.servicos = [...this.form.servicos]
            obj.servicos = obj.servicos.map(x => {
                x.aprovado = 'APROVADO'
                x.aprovado_em = moment()
                return x;
            })
            
            let request = null;
            if(obj.id)    
                request = ordemServicoService.atualizar(obj);
            else
                request = ordemServicoService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    
                    this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + response.data.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(response.data.id);
                    }else{
                        this.buscarObjeto(response.data.id);
                    }
                }   
            ).catch( erro => this.erros = erro )

        },

        
        adicionarServico: function(){     
            setTimeout(() => {
                this.form.servicos.push(
                    {
                        servico_item_id: null,
                        servico_item: null,
                        aprovado: 'PENDENTE',
                        valor_unitario: 0,
                        qtde: 0,
                        valor: 0,
                        descricao: ''
                    }
                );    
            }, 200); 
        },


        removerServico: function(index){     
            setTimeout(() => {
                this.form.servicos.splice(index, 1);    
            }, 200); 
        },

        selecionouServico:function(servico){
            setTimeout(() => {
                console.log(servico, servico.servico_item);
                servico.servico_item_id = servico.servico_item.id;
                let valor_unitario = servico.servico_item.valor_unitario;
                if(this.cliente_tem_tabela(servico)){
                    
                    valor_unitario = this.valor_unitario_cliente(servico);
                    servico.valor_unitario = valor_unitario;
                    servico.valor_selecionado = 'Cliente';
                    servico.servico_item.valor_unitario_cliente = valor_unitario;
                }else{
                    servico.valor_selecionado = 'Tabela';
                    servico.valor_unitario = valor_unitario;
                    servico.servico_item.valor_unitario_cliente = 0;
                }                
                
            }, 10);
        },

        qtde: function(decimais, suffix){
            return {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: ' ' + suffix,
                precision: decimais,
                masked: false
            }
        },

        buscarObjeto: function(id){
            ordemServicoService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    let i = this.tipos_item.map(x => x.id).indexOf(this.form.tipo_item_os_id);
                    this.form.tipo_item_os = this.tipos_item[i];
                    
                }).catch( erro => this.erros = erro)
        },

        buscarParceirosDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarParceiros(text, loading)
            }, 700);
        },

        buscarParceiros: function(text, loading){
            this.buscandoParceiros = true;
            parceiroService.listar({texto: text, per_page: 50, id: this.form.id, tipo: 'CLIENTE'})
                .then( response =>  this.parceiros = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoParceiros = false)
        },

        buscarClienteParametro () {
            if(this.$route.query.cliente_id){
                parceiroService.buscar(this.$route.query.cliente_id)
                    .then(response => {
                        this.parceiros = [response.data]
                        this.form.parceiro = this.parceiros[0]
                        this.form.parceiro_id = this.form.parceiro.id
                        this.buscarTabelaServicosCliente();
                    })
            }
            else{
                this.buscarParceiros()
            }
                
        },

        
       
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },


        novoParceiro: function(){
            this.sucessoItemModal = null;
            this.erroItemModal = null;
            this.parceiroModal = {
                ativo: true,
                cliente: true,
                fornecedor: false,
                tipo_pessoa: 'FISICA',
                estrangeiro: false,
                fone: ''
            }
        },
        
        salvarParceiro: function(){
            this.$v.parceiroModal.$touch();
             this.sucessoItemModal = null;
            this.erroItemModal = null;

            if (this.$v.parceiroModal.$invalid){
                this.erroItemModal = this.$v.parceiroModal;
                return false;
            }
            let obj = {...this.parceiroModal};
            
            parceiroService.criar(obj).then(
                response => {                    
                    this.parceiros = [response.data];
                    this.form.parceiro = this.parceiros[0];
                    this.form.parceiro_id = this.form.parceiro.id;
                    this.sucessoItemModal = "Cliente salvo com sucesso!";
                    
                }   
            ).catch( erro => this.erroItemModal = erro )
        },

        buscarServicosItem: function(){
            servicoItemService.listar({ativo: 1, per_page:1000}).then( response => {
                this.servicos_item = response.data.data.map(x => {
                    x.valor_unitario = Number(x.valor_unitario);
                    return x;
                });
            }).catch(error => {
                console.log('Erro ao buscar Serviços Itens: ', error, error.response);
                setTimeout(() => {
                    this.buscarServicosItem();
                }, 2000);
            })
        },

        cliente_tem_tabela: function(servico){
            
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return false;

            let i = this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id) 
            if(i < 0)
                return false
            let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id)];
            return servico_cliente.pivot.valor_unitario > 0
        },

        valor_unitario_cliente: function(servico){
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return -1;

             let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(servico.servico_item_id)];
             
             if(servico_cliente.pivot.valor_unitario > 0){
                return servico_cliente.pivot.valor_unitario
             }
                
            return servico_cliente.valor_unitario
        },

        selecionouCliente () {
            this.form.parceiro_id = this.form.parceiro.id;  
            this.buscarTabelaServicosCliente();
            this.buscarSaldoCliente();
        },

        buscarSaldoCliente(){
            this.saldo_cliente = 0;
            caixaService.buscarSaldoParceiro(this.form.parceiro.id)
                .then(response => {
                    this.saldo_cliente = response.data[0].saldo
                    if(this.saldo_cliente < 0)
                        Vue.$toast.error(`Atenção! Este cliente está com saldo negativo!`);

                    if(this.saldo_cliente > 0)
                        Vue.$toast.success(`Cliente com saldo disponível!`);
                })
                .catch(error => {
                    Vue.$toast.error(`Não foi possível carregar o saldo do parceiro`);
                    console.log("Erro ao buscar saldo do parceiro", error, error.response);
                    setTimeout(() => {
                        this.buscarSaldo();
                    }, 2000);
                })
        },

        buscarTabelaServicosCliente: function(){
            setTimeout(() => {
                parceiroService.buscarTabelaServicos(this.form.parceiro_id).then( response =>{
                    this.form.parceiro.tabela_servicos = response.data.map(x => {
                        x.pivot.valor_unitario = Number(x.pivot.valor_unitario)
                        x.valor_unitario = Number(x.valor_unitario)
                        return x
                    })
                })    
            }, 800);
            
        },
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },
    


    },
    mounted() {

        setTimeout(() => {
            this.buscarServicosItem();
        }, 50);

        setTimeout(() => {
            this.buscarClienteParametro();
        }, 100);

        // setTimeout(() => {
        //     this.buscarParceiros()
        // }, 400);

        setTimeout(() => {
            this.buscarTabelaServicosCliente()
        }, 500);
        
    },
    computed:{
        editando(){
            return this.$route.params.id > 0;
        },

        precisa_confirmar_valor_zerado(){
            return this.$v.form.$dirty && this.form.servicos.filter(x => x.valor <= 0).length > 0
        },

        itens_por_tipo(){
            let itens = this.form.servicos.filter(x => x.servico_item_id != null ).map(x => x.servico_item_id);
            itens = [...new Set(itens)];
            
            itens = itens.map(x => {
                
                let itens_total = this.form.servicos.filter(i => i.servico_item_id == x);
                let ret = {
                    servico_item : itens_total[0].servico_item,
                    qtde_total : itens_total.reduce( (a,c) => Number(a) + Number(c.qtde), 0),
                    valor_total : itens_total.reduce( (a,c) => Number(a) + Number(c.valor), 0),
                };
                return ret;
            })
            return itens
        },

        valor_total(){
            return this.form.servicos.reduce((a,c) => Number(a) + Number(c.valor), 0)

        }
    }

   
};
</script>
