    width: 110px;
    margin: 0px;
    padding: 1px;
    border: none;
    text-align: right;
}

table th, .table td {
        padding: 3px 5px 3px 0px;
}

.label-preco{
    margin-top: 0px;
    padding-top: 0px !important;
}
